:root {
  --spacex-blue: #005288;
}

.my-row-2-temp {
  display: grid;
  grid-template-rows: 90% 10%;
}

.my-row-1 {
  display: grid;
  grid-row: 1;
}

.my-row-2 {
  display: grid;
  grid-row: 2;
}

.my-col-2-temp {
  display: grid;
  grid-template-columns: 50% 50%;
}

.my-col-1 {
  display: grid;
  grid-column: 1;
}

.my-col-2 {
  display: grid;
  grid-column: 2;
}
